@charset "utf-8";
@import "./include-media.scss";
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Open+Sans&display=swap");

// Atoms
@import "./card.scss";

// Components
@import "./navbar.scss";
@import "./header.scss";
@import "blog";
@import "./about.scss";
@import "./skills.scss";
@import "./promotion.scss";
@import "./footer.scss";

* {
  font-family: "Montserrat";
}

body {
  margin: 0;
  padding: 0;
  overflow-y: scroll;
  overflow-x: hidden;
   background-image: linear-gradient(to bottom right, #fadfdf, #f3f3d7, #9afc9b);
}

.section {
  width: 100vw;
  height: auto;
  overflow: hidden;
}
.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
 @include media(">=phone", "<lgphone") {
   .nav-container {
      margin-top: 0;
      align-items: flex-start;
    }
 }
.primary-btn {
  margin: 8px;
  padding: 8px;
  background-color: black;
  color: white;
  border: 0px;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  cursor: pointer;
  font-size: 11px;
  &:hover {
    transform: scale(1.1);
  }

}
p {
  text-align: justify;
  word-break: break-all;
  hyphens: auto;
  font-size: 14px;
  @include media("<desktop") {
    text-align: justify;
    word-break: break-all;
    hyphens: auto;
    font-size: 12px;
  }
}