.skills-container {
  font-size: 14px;
  width: 70vw;
  margin-top: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 3rem;
    line-height: 20px;
  }

  .skills-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 20px;
    grid-gap: 20px;
    @include media(">=phone", "<lgphone") {
      grid-template-columns: 1fr;
    }
    @include media(">=lgphone", "<tablet") {
      grid-template-columns: 1fr;
    }
    @include media(">=tablet", "<desktop") {
      grid-template-columns: 1fr 1fr;
    }

    .skill {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;

      img {
        height: 3rem;
      }

      p {
        font-size: 12px;
        text-align: justify;
      }
    }
  }
}
