.section {
  width: 100vw;
  height: auto;
  overflow: hidden;
}

.navbar-wrapper {
  cursor: pointer;
  z-index: 999;
  background-color: #323334;
  color: #d7d7d7;
  position: fixed;
  height: 3.5rem;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;


  .name {
    font-weight: 1000;
    font-size: 2rem;
    color: #d7d7d7;
    text-decoration: none;
    padding: 8px;
    transition: all 0.2s ease-in-out;
    outline: none;
    border: 0px;
    cursor: pointer;
    align-items: center;
    opacity: 0.6;

  }

  .links-wrapper {
    .link-selector {
      color: #d7d7d7;
      font-weight: bold;
      text-decoration: none;
      padding: 10px;
      background-color:  #323334;
      opacity: 0.6;
      transition: all 0.2s ease-in-out;
      font-size: .8rem;
      outline: none;
      border: 0px;
      cursor: pointer;
      align-items: center;

      &:hover {
        background-color: #414141;
        opacity: 1;
      }
    } .active {
        background-color: #181cfd;
        opacity: 1;
        transition: all 0.2s;
        font-size: 1.8rem;
        text-transform: uppercase;

      }
  }
}
.ham-menu {
      display: none;
    }
  @include media( "<lgphone") {
    .name{
      opacity: 0 !important;
    }
    .show-from-mob{
      margin-left:0 !important;
      transition: margin-left .6s linear;
    }
    .ham-menu {
      padding: 20px;
      color: #f3f3d7;
      z-index: 999;
      background: #242424;
      display:block;
      position: absolute;
    }
    .ham-menu:hover{
         background: #000000;
      }
    .navbar-wrapper {
      display: block;
      align-content: flex-start;
      margin-top: 0;
      justify-content: normal;
        width: 100vw;
      .links-wrapper{
        margin-left: -50vw ;
        transition: margin-left .6s linear;
        height:500vw !important;
        width: 50vw;
        background-color: #323334;
        opacity: 0.7;
        .link-selector {
          display: block;
          width: 44vw;
        }
      }
    }


  }
  @include media(">=lgphone", "<tablet") {

